import React, { useState, useEffect } from "react";
import Draggable from "react-draggable";
import classNames from "classnames";
import "./utilToolkit.scss";

const UtilToolkit = () => {
  const [editMode, setEditMode] = useState(false);
  const [position, setPosition] = useState({ x: 887, y: 946 });
  const [throttle, setThrottle] = useState(false);

  const [on, setOn] = useState(false);

  const trackPos = (data) => {
    setPosition({ x: data.x, y: data.y });
  };

  const toggleEditMode = () => {
    // e.stopPropagation()
    if (editMode === false) {
      setEditMode(true);
      document.designMode = "on";
      // document.body.style.pointerEvents = "none";
    } else {
      setEditMode(false);
      document.designMode = "off";
      // document.body.style.pointerEvents = "";
    }
  };

  window.addEventListener("keydown", keysPressed, false);
  window.addEventListener("keyup", keysReleased, false);

  var keys = [];

  function keysPressed(e) {
    keys[e.keyCode] = true;
    if (keys[16] && keys[112]) {
      e.preventDefault();
      // console.log("Shift + F1");
      setOn(true);
    }
    if (keys[16] && keys[113]) {
      e.preventDefault();
      // console.log("Shift + f2");
      setOn(false);
    }
  }

  function keysReleased(e) {
    keys[e.keyCode] = false;
  }

  // TODO:   body pointer-event none ,   hover  body  pointer-event none 

  return (
    <div>
      {
        // (window.location.href.includes(":3000/") ||
        // window.location.href.includes("-dev")) &&
        on && (
          <Draggable onDrag={(e, data) => trackPos(data)}>
            <div
              className={classNames("tool-kit", { "is-none-event": editMode })}
            >
              <button
                className={classNames("editmode", { "is-edit": editMode })}
                onClick={() => toggleEditMode()}
              >
                 : {editMode ? "ON" : "OFF"}
              </button>
              <div className="list-style-none">
                <div className="sub-title">
                  {/* <a href="#/component">-  </a> */}
                </div>
                <ul>
                  <li>
                    <a href="http://localhost:3000/#/component">
                      -  
                    </a>
                  </li>
                  <li>
                    <a href="http://localhost:3000/#/popup">-  </a>
                  </li>
                </ul>
                <h4> </h4>
                <dl>
                  <dt>
                    <strong></strong>
                  </dt>
                  <dd>
                    <p>
                           rgba
                      , rgba  uitoolki   
                      <br />
                      https://codepen.io/urosurosevic/pen/pagxYE
                    </p>
                  </dd>
                  <dt>
                    <strong>  </strong>
                  </dt>
                  <dd>f5    ..</dd>
                </dl>
              </div>
            </div>
          </Draggable>
        )
      }
    </div>
  );
};

export default UtilToolkit;
