import React, { useContext, useReducer } from "react";
import { CountContext } from "../../context/Count";
import ContainerQuery from "../../components/containerQuery/ContainerQuery";
import Switch from "../../components/switch/Switch";

const Etc = () => {
  function reducer(state, action) {
    switch (action.type) {
      case "INCREMENT":
        return state >= 0 ? state + 1 : 0;
      case "DECREMENT":
        return state > +0 ? state - 1 : 0;
      default:
        return state;
    }
  }

  //       
  const [number, dispatch] = useReducer(reducer, 0);
  const { numbers, plusCount } = useContext(CountContext);

  const onIncrease = () => {
    dispatch({ type: "INCREMENT" });
  };

  const onDecrease = () => {
    dispatch({ type: "DECREMENT" });
  };

  const style = {
    background: 'red',
    color: 'white'
  }

  const style2 = {
    background: 'blue',
    color: 'white'
  }
  return (
    <div>
      <h2>   </h2>
      <h3> </h3>
      <button onClick={onIncrease}>+1</button>
      <button onClick={onDecrease}>-1</button>
      <div>{number}</div>

      <h3> </h3>
      <button onClick={plusCount}>+ 1</button>
      <div>{numbers}</div>

      <h3> </h3>
      <a
        href="https://developer.mozilla.org/en-US/docs/Web/CSS/CSS_Container_Queries"
        target="_blank"
        rel="noreferrer"
      >
        css    js 
      </a>
      <p> div     div css </p>
      {/*  id   */}
      <ContainerQuery classname="content-container" size={800} style={style}>
        1 size: 800
      </ContainerQuery>

      <ContainerQuery classname="content-container2" size={1200} style={style2}>
        2 size: 1200
      </ContainerQuery>

      <h3> </h3>
      <p>    </p>
      <Switch/>
    </div>
  );
};

export default Etc;
